@import "./../base/_base.scss";

.calendarComponent {
  width: $container;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  position: relative;
  z-index: 100;
  margin-top: 82px;

  @include tablet {
    width: $container-tablet;
    flex-direction: column;
    margin-top: 140px;
  }

  @include phone {
    width: 85%;
    overflow-x: scroll;
    margin-left: 2rem;
  }

  .weekDays {
    display: flex;
    flex-direction: column;
    width: 25%;
    @include phone {
      width: 281px;
    }
    h3 {
      font-style: normal;
      font-weight: 700;
      font-size: 40px;
      line-height: 48px;
      text-align: center;
      color: #084887;
      margin-bottom: 24px;

        @include phone{
          font-size: $size*4!important;
          line-height: $size*5!important;
          font-weight: 700!important;
        }

    }
    article {
      border-radius: 10px;
      padding: 16px;
      background-color: #fff;

      .weeks {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        &:last-child {
          .week {
            border-bottom: 0;
          }
          .days {
            border-bottom: 0;
           
          }
        }
        .week {
          display: flex;
          align-items: center;
          width: 160px;
          border-right: 1px solid #ccc;
          border-bottom: 1px solid #ccc;
          padding: 24px;
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 36px;
          @include phone{
           text-align: center;
           padding: 16px;
          }
        }

        .days {
          width: 146px;
          display: flex;
          flex-direction: column;
          align-items: center;
          border-bottom: 1px solid #ccc;
          span {
            display: flex;
            align-items: center;
            height: 80px;
            border-bottom: 1px solid #ccc;
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 36px;
            @include phone{
              font-size: $size*3!important;
             
            }
            &:last-child {
              border-bottom: 0;
            }
          }
        }
      }
    }
  }

  .classHours {
    width: 75%;
  
    display: flex;
    flex-direction: column;
    margin: 24px 20px;
    @include phone {
      width: 848px;
      margin: 8px 20px 24px;
    }
    .hours{
        background-color: #fff;
        padding: 24px;
        border-radius: 10px;
        margin-bottom: 24px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .item{
            display: flex;
            flex: 1;
            border-right: 1px solid #ccc;
            justify-content: center;
            &:last-child{
                border-right: 0;
            }
        }
    }
    .courses{
        margin-bottom: 24px;
        display: flex;
        flex-direction: column;
        .week-days{
            box-sizing: border-box;
            height: 168px;
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
        
        }
        .dayOne{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            .item-1{
                display: flex;
                justify-content: center;
                align-items: center;
                width: 25%;
                height: 68px;
                background-color: #fff;
                padding: 16px;
                border-radius: 10px;
                margin-right: 20px;
                font-weight: 300;
                box-sizing: border-box;
                font-size: 14px;
                line-height: 20px;
                text-align: center;
                color: #031D44;
              @include phone{
                width: 200px;
              }
                &:last-child{
                    margin-right: 0px; 
                }
            }
            .item-2{
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                width: 50%;
                height: 68px;
                padding: 16px;
                border-radius: 10px;
                margin-right: 20px;
                box-sizing: border-box;
                font-size: 14px;
                line-height: 20px;
                color: #031D44;
                background-color: #fff;
                @include phone{
                  width: 416px;
                }
                &:last-child{
                    margin-right: 0px; 
                }
            }
        }
      
    }
  }
}

.highlights{
    background-color:  rgba(255, 216, 117, 0.38)!important;
}